body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

._nav {
    width: 100%;
}
._db {
    display: inline-block;
}

._mg {
    margin: .5rem;
}

._pd {
    padding: .2rem;
}

._iul {
    display: none;
}

ul._ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #f9f9f9;
}

ul._ul ._right {float: right;}

ul._ul li a {
    display: block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    color: black;
    font-size: larger;
    font-family: sans-serif;
}

ul._ul li a:hover:not(.active) {color: #f78769;}

ul._ul li a.active {background-color: #4CAF50;}

ul._ul li.right {float: right;}

@media screen and (max-width: 600px){
    ul._ul li.right,
    ul._ul li {float: none;}
}

._iul {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

._iul a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

._dpdn:hover ._iul {display: block;}

.header {
    display: inline-block;
}

.logo {
    font-family: sans-serif;
    font-weight: bold;
    font-style: italic;
    font-size: large;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

._lgmg {
    margin-left: 2rem;
}

.header-tag {
    position: fixed;
    right: 0;
    left: 0;
    z-index: 999;
    top: 0;
}

.logo-icon {
    color: #24afa0;
}
._bd-container {
    margin: 1rem;
    padding: 1rem;
}

._img-div {
    width: 48%;
    display: inline-block;
    margin-top: 3rem;
    border: solid aliceblue .25px;
    margin-left: .25rem;
}

._img {
    width: 97%;
    height: 15rem;
    margin: .25em;
}


._img-container {
    width: 70%;
    display: inline-block;
}

._aside-container {
    display: inline-block;
    top: 5rem;
    position: absolute;
}
._aside-container input[type=text] {
    padding: 6px;
    margin-top: 8px;
    font-size: 17px;
    border: none;
}

._aside-container .search-container button {
    padding: 6px 10px;
    margin-top: 8px;
    margin-right: 16px;
    background: #ddd;
    font-size: 17px;
    border: none;
    cursor: pointer;
}

._as-img {
    width: 97%;
    height: 15rem;
    margin: .25em;
    z-index: 99999;
    border-radius: 1rem ;
}
._as-img-con {
    margin: .5rem
}

._as-img-div {
    margin-bottom: 2rem;
    box-shadow:
    4px 1px 2px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

._as-img-div:hover {
    box-shadow:
            3px 1px 2px 3px rgba(0.3, 0.3, 0.3, 0.3), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

._pl-name {
    text-align: center;
}

._rec-div {
    height: 100rem;
    overflow-y: scroll;
}

::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #e5ebf4;
    border-radius: 10px;
}

